@import 'normalize';
@import 'sprite';
@import 'owl.carousel';
@import url(http://fonts.googleapis.com/css?family=Open+Sans:300italic,400italic,700italic,400,300,700&subset=latin,cyrillic-ext,cyrillic);
$maincolor: #222;
$redcolor: #b01e36;
$greycolor: #a29a9a;
$greencolor: #57a31b;
$trs: all 2s;
$bdrs: 5px;

* {
           box-sizing: border-box;
   -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
   -webkit-font-smoothing: antialiased;
           font-smoothing: antialiased;
   text-rendering: optimizeLegibility;
   &:after,
   &:before {
              box-sizing: border-box;
      -webkit-box-sizing: border-box;
         -moz-box-sizing: border-box;
      -webkit-font-smoothing: antialiased;
              font-smoothing: antialiased;
      text-rendering: optimizeLegibility;
   }
}

h1,
h2,
h3,
h4,
h5,
h6 {
   margin: 0;
   font-weight: 400;
}
p {
   margin: 0 0 18px;
}
fieldset {
   margin: 0;
   padding: 0;
}
a,
input,
button {
   transition: $trs;
   outline: none;
}
input,
textarea {
   border: 1px solid #ccd4d8;
   border-radius: $bdrs;
   padding: 13px;
   font-size: 16px;
   color: #929191;
   &:hover,
   &:focus,
   &:active {
      border-color: darken(#ccd4d8, 25%);
   }
}
body {
   font-family: 'Open Sans', sans-serif;
   font-weight: 400;
   font-size: 16px;
   color: $maincolor;
   font-weight: 300;
}
.inner {
   position: relative;
   width: 980px;
   margin: 0 auto;
}
/* clearfix */
.clearfix {
   &:before {
      display: table;
      content: '';
   }
   &:after {
      display: table;
      content: '';
      clear: both;
   }
}
.clear {
   clear: both;
}
.clearfix {
   *zoom: 1;
}

img {
   max-width: 100%;
   vertical-align: bottom;
}

/*------------------------------------*\
    STRUCTURE
\*------------------------------------*/

/* wrapper */
.wrapper {
   width: 100%;
   margin: 0 auto;
   position: relative;
}
header,
article {
   width: 980px;
   margin: 0 auto;
}

header {
   height: 120px;
   nav,
   .logo,
   .lang-selector,
   .phone {
      display: inline-block;
      float: left;
      height: 100%;
   }
   .logo {
      width: 116px;
      display: inline-block;
      padding: 10px 0 0;
      img {
         width: 116px;
         height: 100px;
      }
   }
   nav {
      width: 430px;
      margin: 0 4px 0 0;
      ul {
         display: block;
         padding: 50px 0;
         text-align: center;
         li {
            display: inline-block;
            margin: 0 10px;
            font-size: 15px;
            font-weight: 300;
            a {
               text-decoration: none;
               color: black;
               &:hover {
                  color: $redcolor;
               }
            }
         }
      }
   }
   .lang-selector {
      opacity: 0;
      width: 250px;
      padding: 45px 0 0;
      text-align: center;
      position: relative;
      li {
         display: none;
         width: 100px;
         height: 40px;
         text-align: center;
         margin: 0 auto;
         border: 1px solid $greycolor;
         position: relative;
         z-index: 50;
         list-style: none;
         a {
            display: block;
            padding: 10px 0;
            width: 100%;
            height: 100%;
            text-decoration: none;
            color: black;
         }
         &:before {
            content: '';
            display: block;
            position: absolute;
            top: 10px;
            left: 10px;
            @include sprite($sprite-ico-lang-rus);
         }
         &:first-child {
            display: block;
            border: 1px solid transparent;
            &:after {
               content: '';
               display: block;
               position: absolute;
               top: 18px;
               right: 10px;
               @include sprite($sprite-ico-arrow-bottom);
            }
         }
         &:nth-child(2) {
            border-top: 1px solid transparent;
            border-bottom: 1px solid transparent;
            margin-top: -2px;
            &:before {
               @include sprite($sprite-ico-lang-eng);
            }
         }
         &:last-child {
            border-top: 1px solid transparent;
            margin-top: -2px;
            &:before {
               @include sprite($sprite-ico-lang-geo);
            }
         }
      }
      &:hover {
         li {
            display: block;
            &:first-child {
               border: 1px solid $greycolor;
               border-bottom: 1px solid transparent;
               &:after {
                  @include sprite($sprite-ico-arrow-top);
               }
            }
         }
      }
   }
   .phone {
      width: 180px;
      padding: 50px 0 0 20px;
      color: black;
      text-align: left;
      text-decoration: none;
      font-size: 21px;
      color: #333;
      position: relative;
      &:before {
         display: block;
         content: '';
         @include sprite($sprite-ico-phone);
         position: absolute;
         top: 55px;
         left: 0;
      }
      &:after {
         content: 'звоните круглосуточно';
         color: $greycolor;
         position: absolute;
         left: 22px;
         top: 70px;
         font-size: 12px;
         font-weight: 300;
      }
   }
}
.home-slider {
   width: 100%;
   height: 610px;
   overflow: hidden;
   .forcefullwidth_wrapper_tp_banner {
      height: 610px !important;
   }
   .go-bottom {
      height: 1px;
      width: 980px;
      margin: -1px auto 0;
