      a {
         display: block;
         width: 44px;
         height: 44px;
         border-radius: 50%;
         position: absolute;
         left: 468px;
         bottom: 155px;
         background-color: black;
         z-index: 100;
         &:after {
            display: block;
            content: '';
            @include sprite($sprite-ico-arrow-bottom-big);
            position: absolute;
            top: 15px;
            left: 12px;
            transition: all 1s;
