      margin: 0 auto;
      background-color: transparent;
      .lazy-selector {
         text-align: center;
         color: #292929;
         font-size: 40px;
         margin: 0 auto 16px;
      }
   }
}
.all-review-page {
   display: block;
   margin: 0 auto;
   padding: 0;
   list-style-type: none;
   li {
      display: block;
      padding: 20px;
      position: relative;
      &:nth-child(odd) {
         background-color: #fff;
      }
   }
}
.paginugets {
   text-align: center;
   a,
   span {
      display: inline-block;
      margin: 0 5px;
   }
}
::selection,
::-webkit-selection,
::-moz-selection {
   background: #04a4cc;
   color: #fff;
   text-shadow: none;
}
/*------------------------------------*\
    WORDPRESS CORE
\*------------------------------------*/
.alignnone {
   margin: 5px;
}
.aligncenter,
div.aligncenter {
   display: block;
   margin: 5px auto;
}
.alignright {
   float: right;
   margin: 5px 0 20px 20px;
}
.alignleft {
   float: left;
   margin: 5px 20px 20px 0;
}
.wp-caption {
   background: #fff;
   border: 1px solid #f0f0f0;
   max-width: 96%;
   padding: 5px 3px 10px;
   text-align: center;
   img {
      border: 0 none;
      height: auto;
      margin: 0;
      max-width: 98.5%;
      padding: 0;
      width: auto;
   }
   .wp-caption-text {
      font-size: 80%;
      line-height: 17px;
      margin: 0;
      padding: 0 4px 5px;
   }
}
.gallery-caption {
   font-size: 80%;
   line-height: 17px;
   margin: 0;
   padding: 0 4px 5px;
}

/*------------------------------------*\
    PRINT
\*------------------------------------*/
@media print {
   * {
      background: transparent !important;
      color: #000 !important;
      box-shadow: none !important;
      text-shadow: none !important;
   }
   a {
      text-decoration: underline;
      &:visited {
         text-decoration: underline;
      }
      &[href]:after {
         content: ' (' attr(href) ')';
      }
   }
   abbr[title]:after {
      content: ' (' attr(title) ')';
   }
   .ir a:after {
      content: '';
   }
   a {
      &[href^='javascript:']:after,
      &[href^='#']:after {
         content: '';
      }
   }
   pre,
   blockquote {
      border: 1px solid #999;
      page-break-inside: avoid;
   }
   thead {
      display: table-header-group;
   }
   tr {
      page-break-inside: avoid;
   }
   img {
      page-break-inside: avoid;
      max-width: 100% !important;
   }
   @page {
      margin: .5cm;
   }
   p,
   h2,
   h3 {
      orphans: 3;
      widows: 3;
   }
   h2,
   h3 {
      page-break-after: avoid;
   }
}
